.hero {
  width: 25rem;
  height: 25rem;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 5;
  border-radius: 40%;
  //animation: heroShadow 10s linear
  transition: box-shadow 20s linear;
 }  //box-shadow: 0 0 0 150rem rgba(0, 0, 0, 1), inset 0 0 1rem rgba(0, 0, 0, 0.95), inset 0 0 0.5rem rgba(0, 0, 0, 0.95)


@keyframes heroShadow {
  0% {
    box-shadow: 0 0 0 150rem rgba(0, 0, 0, 0.1), inset 0 0 1rem rgba(0, 0, 0, 0.95), inset 0 0 0.5rem rgba(0, 0, 0, 0.95); }
  100% {
    box-shadow: 0 0 0 150rem rgba(0, 0, 0, 1), inset 0 0 1rem rgba(0, 0, 0, 0.95), inset 0 0 0.5rem rgba(0, 0, 0, 0.95); } }


.body {
  position: relative;
  width: 6rem;
  height: 6rem;
  border-radius: 2rem; }

.head {
  width: 50px;
  height: 50px;
  background: url('../img/alien.png');
  background-size: cover;
  position: absolute;

  top: -2px;
  left: 2px;
  z-index: 5;

  &--left {
    transform: rotate(10deg); }

  &--right {
    transform: rotate(-10deg); } }


.leg_one {
  width: 50px;
  height: 50px;
  position: absolute;
  background: url('../img/leg.png');
  z-index: 3;

  &--right {
    top: 10px;
    left: 3px; }

  &--left {
    top: 10px;
    left: 10px;
    transform: scaleX(-1); } }


.leg_two {
  width: 50px;
  height: 50px;
  position: absolute;
  background: url('../img/leg.png');
  z-index: 3;

  &--right {
    top: 12px;
    left: 10px; }

  &--left {
    top: 12px;
    left: 5px;
    transform: scaleX(-1); } }


.running {
  .leg_one--right {
    animation: runRightStart ease-in 0.25s, runRight 0.5s ease-in 0.25s infinite; }
  .leg_two--right {
    animation: runRight 0.5s ease-in infinite; }
  .leg_one--left {
    animation: runLeftStart ease-in 0.25s, runLeft 0.5s ease-in 0.25s infinite; }
  .leg_two--left {
    animation: runLeft 0.5s ease-in infinite; } }

@keyframes runRightStart {
  100% {
    top: 12px;
    left: 10px; } }

@keyframes runRight {
  0% {
    top: 12px;
    left: 10px; }
  20% {
    top: 12px;
    left: 5px;
    transform: rotate(10deg); }
  40% {
    top: 7px;
    left: 7px; }
  60% {
    top: 7px;
    left: 7px;
    transform: rotate(-10deg); }
  80% {
    top: 12px;
    left: 10px; }
  100% {
    top: 12px;
    left: 10px; } }

@keyframes runLeft {
  0% {
    top: 12px;
    left: 5px; }
  20% {
    top: 12px;
    left: 10px;
    transform: rotate(-10deg) scaleX(-1); }
  40% {
    top: 7px;
    left: 7px; }
  60% {
    top: 7px;
    left: 7px;
    transform: rotate(+10deg) scaleX(-1); }
  80% {
    top: 12px;
    left: 5px; }
  100% {
    top: 12px;
    left: 5px; } }

@keyframes runLeftStart {
  100% {
    top: 12px;
    left: 5px; } }
