@import "base.sass";

.app {
  display: flex;
  flex-direction: column; }

.games {
  display: flex;
  justify-content: center;
  margin-top: 1rem; }

.games__menu--alone {
  display: none; }

.games__menu,
.games__menu--alone {
  font-family: 'Sacramento-Regular', sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  height: 70rem;
  font-size: 5rem;
  padding: 0 1rem;
  border-radius: 20px;
  margin: 0.5rem 0.5rem 0.5rem 0;
  box-shadow:  0 0 5px rgba(255,255,255, 0.3); }

@media (max-width: $media) {
    .games__menu {
      display: none; } }

.game__link {
  width: 30rem;
  text-decoration: none;
  color: $blueNeonColor;
  border-radius: 20px;
  box-shadow:  0 0 5px rgba(255,255,255, 0.3);
  padding: 2rem 2rem;
  margin: 1rem 0;

  &:hover {
    box-shadow:  0 0 15px rgba(255,255,255, 0.5); }

  &--active {
    animation: blueNeon .1s ease-in-out infinite alternate; } }

.not-found {
  font-family: "TextMeOne", sans-serif;
  font-size: 15rem;
  text-align: center;
  padding: 5rem;
  color: $blueNeonColor;
  text-shadow: 0 0 5px $blueNeonColor; }
