.login, .logon {
  font-family: "TextMeOne", sans-serif;
  width: 50rem;
  height: 60rem;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  font-size: 5rem;
  color: rgba(255,255,255,0.9);

  .logon__input, .login__input {
    margin: 0.3rem 0;
    width: 40rem;
    height: 5rem;
    padding: 0 1rem;
    border: none;
    outline: none;
    border-radius: 5px;
    font-size: 2rem;
    background: rgba(255,255,255,0.9);
    box-shadow: 0 0 10px rgba(255,255,255,0.7);

    &:disabled {
      background: rgba(255,255,255,0.3); }

    &--valid {
      background: rgba(215, 234, 255, 0.9);
      box-shadow: 0 0 15px rgb(131, 190, 255); }

    &--error {
      background: rgba(255, 220, 218, 0.9);
      box-shadow: 0 0 15px rgba(255, 82, 75, 1); } }

  .logon__input-title {
    width: 40rem;
    font-size: 2rem;
    line-height: 2rem;
    margin-top: 1.4rem;

    &--valid {
      color: #b5e3ff; } }

  .submit-button {
    font-family: "TextMeOne", sans-serif;
    width: 16rem;
    height: 5rem;
    background: none;
    border: 1px solid rgba(255,255,255,0.7);
    background: rgba(0, 0, 0, 0.5);
    border-radius: 10px;
    color: rgba(255,255,255,0.9);
    outline: none;
    margin: 2rem 0;
    font-size: 3rem;
    cursor: pointer;
    box-shadow: inset -1px 2px 5px rgba(255,255,255,0.9), 0 2px 5px rgba(255,255,255,0.7);

    &:active {
      transform: translateY(1px);
      box-shadow: inset -1px 2px 5px rgba(255,255,255,0.9), 0 1px 5px rgba(255,255,255,0.7); }

    &:disabled {
      border: 2px solid rgba(255,255,255,0.1);
      color: rgba(255,255,255,0.2);
      box-shadow: inset -1px 1px 5px rgba(255,255,255,0.2), 0 2px 5px rgba(255,255,255,0.2);
      cursor: auto; } }

  .message {
    height: 1.7rem;
    width: 40rem;
    line-height: 1.7rem;
    font-family: "TextMeOne", sans-serif;
    font-size: 1.4rem;

    &--error {
      color: #ff7879; } } }
