@import "../base.sass";

.coinsGame {
  font-family: "TextMeOne", sans-serif;
  font-size: 2.5rem;
  display: flex;
  justify-content: space-around;
  height: 70rem;
  border: 5px solid transparent;
  color: #4ba7fc;
  width: 154rem;
  @media (max-width: $media) {
    justify-content: space-around;
    height: auto; }
  @media (max-width: 1200px) {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    order: -1;
    height: auto; }

  &__gameField {
    margin-left: 10rem;
    height: 700px;
    width: 800px;
    background: url("../img/bg.jpg") center center no-repeat;
    background-size: cover;
    border-radius: 20px;
    box-shadow: 0 0 10px rgba(180, 180, 180, 0.5);
    position: relative;
    overflow: hidden;
    @media (max-width: $media) {
      margin: 0; } }

  &__gameOverNotification,
  &__levelNotification {
    width: 50rem;
    margin: 23rem auto;
    color: rgba(255, 255, 255, 0.84);
    background: rgba(0,0,0,0.7);
    padding: 2rem;
    text-align: center;
    border-radius: 20px;
    box-shadow: 1px 1px 5px 2px rgba(0,0,0,1);
    position: relative;
    z-index: 5;
    font-size: 4rem; }

  &__helloPage {
    width: 50rem;
    margin: 15rem auto;
    color: rgba(255, 255, 255, 0.84);
    background: rgba(0,0,0,0.5);
    padding: 2rem;
    text-align: center;
    border-radius: 20px;
    box-shadow: 1px 1px 5px 2px rgba(0,0,0,1);
    position: relative;
    z-index: 5;

    &__GL {
      text-align: center;
      font-size: 3rem;
      color: rgba(255, 223, 241, 0.82); } }

  &__startLevel,
  &__restartGame,
  &__startLevel {
    display: block;
    margin: 2rem auto 0;
    cursor: pointer;
    font-family: "TextMeOne" ,sans-serif;
    text-decoration: none;
    line-height: 4.5rem;
    padding: 0.3rem 3.5rem 0;
    background: none;
    border: none;
    box-shadow: 0 2px 7px rgba(255,255,255, 0.6);
    outline: none;
    color: #fff;
    text-shadow: 0 0 2px $blueNeonColor;
    font-size: 2.5rem;
    border-radius: 10px;

    &:active {
      transform: translateY(1px);
      box-shadow: 0 0 7px rgba(255,255,255, 0.6); } }

  &__coin {
    font-family: "Times New Roman", serif;
    width: 100px;
    height: 100px;
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    color: rgba(241, 194, 96, 0.78);
    text-shadow: 1px 1px 1px rgba(80, 42, 18, 0.5), -1px 1px 1px rgba(0, 0, 0, 0.5);
    user-select: none;

    &--closed {
      background: url("../img/coin2.png") center center no-repeat;
      background-size: cover;
      cursor: pointer;
      font-size: 0;
      box-shadow: 3px 3px 5px rgba(0,0,0,1);

      &:hover {
        transform: scale(1.01) translate(-1px, -1px);
        box-shadow: 5px 5px 5px rgba(0,0,0,1); } }

    &--opened {
      background-size: cover;
      animation: rotating 0.2s linear;
      background: url("../img/coin1.png") center center no-repeat;
      background-size: cover;
      font-size: 6rem;
      transition: background 0s linear 0.1s, font-size 0s linear 0.1s;
      box-shadow: 3px 3px 5px rgba(0,0,0,1); }

    &--won {
      box-shadow: 0 0 20px rgba(83, 255, 103, 0.7), 0 0 5px rgba(83, 255, 103, 1), 3px 3px 5px rgba(0,0,0,1); }

    &--error {
      box-shadow: 0 0 30px rgba(255, 81, 37, 0.9), 0 0 5px rgb(255, 103, 62), 3px 3px 5px rgba(0,0,0,1); } }

  &__Timer {
    box-shadow: 0 0 5px rgba(255,255,255,0.5);
    height: 1rem;
    background: rgba(255,255,255,0.2);
    transition-property: width;
    transition-timing-function: linear;
    width: 0; }

  &__scoreField {
    color: white;
    width: 50rem;
    padding: 0 2rem;
    @media (max-width: $media) {
      width: 50rem;
      padding: 0; }

    &__currentScores {
      color: #8cd6ff;
      font-size: 5rem;
      line-height: 5rem;
      margin: 2rem 0; }

    &__table {
      width: 50rem;
      text-align: center;
      background: rgba(0,0,0,0.5); }

    &__header {
      border-radius: 10px;
      box-shadow: 0 0 5px rgba(255,255,255, 0.3); } } }


@keyframes rotating {
  0% {
    transform: rotate3d(0, 1, 0, 0deg) scale(1.1, 1.1); }
  50% {
    transform: rotate3d(0, 1, 0, 90deg) scale(1.2, 1.2); }
  100% {
    transform: rotate3d(0, 1, 0, 0deg) scale(1, 1); } }

