@import "base";

.header__log {
  height: 4.5rem;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  @media (max-width: $media) {
    justify-content: center; }

  &-name {
    font-family: "TextMeOne", "Helvetica Neue", sans-serif;
    color: $blueNeonColor;
    font-size: 2rem;
    line-height: 2rem;
    margin: 0 2rem;
    text-decoration: none; } }

.logout-button, .login-link, .logon-link {
  font-family: "TextMeOne" ,sans-serif;
  text-decoration: none;
  line-height: 4.5rem;
  padding: 0.3rem 3.5rem 0;
  margin-left: 1rem;
  background: none;
  border: none;
  box-shadow: 0 2px 7px rgba(255,255,255, 0.6);
  outline: none;
  color: #fff;
  text-shadow: 0 0 2px $blueNeonColor;
  font-size: 2.5rem;
  cursor: pointer;
  border-radius: 10px;

  &:active {
    transform: translateY(1px);
    box-shadow: 0 0 7px rgba(255,255,255, 0.6); } }

.headerNav {
  height: 16rem;
  display: flex;
  justify-content: center;

  &--wideMenu {
    justify-content: space-between; } }

.headerNav__element {
  font-family: "Zapped Sticks", sans-serif;
  display: flex;
  align-items: center;
  height: 16rem;
  padding: 1rem 3rem;
  overflow: hidden; }

.headerNav__link {
  text-decoration: none;
  font-size: 14rem;
  line-height: 14rem;
  padding-top: 1rem;
  color: $turnOffNeonColor;

  &--red:hover {
    color: $pinkNeonPaleColor; } }

.activeRed {
  color: $pinkNeonColor;
  animation: redNeon .1s ease-in-out infinite alternate, pinkNeonTurnOn 0.5s linear;
  &--red:hover {
    color: $pinkNeonColor; } }

.activeBlue {
  color: $blueNeonColor;
  animation: blueNeon .1s ease-in-out infinite alternate, blueNeonTurnOn 0.5s linear; }


@keyframes blueNeon {
  from {
    text-shadow: $blueNeon; }
  to {
    text-shadow: $brightBlueNeon; } }

@keyframes redNeon {
  from {
    text-shadow: $pinkNeon; }
  to {
    text-shadow: $brightPinkNeon; } }

@mixin turnOnAnimationMixin($mainColor, $paleColor, $shadow) {
  0% {
    color: $turnOffNeonColor; }
  60% {
    color: $paleColor; }
  61% {
    text-shadow: none;
    color: $turnOffNeonColor; }
  69% {
    text-shadow: none;
    color: $turnOffNeonColor; }
  70% {
    text-shadow: $shadow;
    color: $mainColor; }
  71% {
    text-shadow: none;
    color: $turnOffNeonColor; }
  99% {
    text-shadow: none;
    color: $turnOffNeonColor; }
  100% {
    text-shadow: $shadow;
    color: $mainColor; } }

@keyframes blueNeonTurnOn {
  @include turnOnAnimationMixin($blueNeonColor, $blueNeonPaleColor, $blueNeon); }

@keyframes pinkNeonTurnOn {
  @include turnOnAnimationMixin($pinkNeonColor, $pinkNeonPaleColor, $pinkNeon); }
