.granny {
  width: 10rem;
  height: 10rem;
  position: absolute;
  background: url("../img/granny-2.png");
  background-size: cover;
  animation: shaking 0.5s ease-in infinite, appearance 0.5s linear; }

@keyframes shaking {
  0% {
   transform: rotate(3deg); }
  50% {
   transform: rotate(-3deg); }
  100% {
   transform: rotate(3deg); } }

@keyframes appearance {
  0% {
    opacity: 0; } }
