.game {
  font-family: 'Sacramento-Regular', sans-serif;
  font-size: 2.5rem;
  position: relative;

  &__menu {
    height: 69rem;
    width: 120rem;
    display: flex;
    border-radius: 20px;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    font-family: "TextMeOne", sans-serif;
    font-size: 5rem;
    background: url('../img/2.jpg');
    border: 5px solid #000;
    color: #fff;
    box-shadow: 0 0 10px rgba(180, 180, 180, 0.5); }

  &__start-button {
    z-index: 5;
    font-family: "TextMeOne", sans-serif;
    margin: 1rem 0;
    padding: 2rem 3rem;
    background: none;
    border: none;
    background: rgba(0, 0, 0, 0.7);
    border-radius: 20px;
    color: rgba(255,255,255,0.9);
    outline: none;
    font-size: 3rem;
    cursor: pointer;
    box-shadow: 0 1px 7px rgba(255,255,255,0.5);

    &:active {
      transform: translateY(1px);
      box-shadow: 0 0 7px rgba(255,255,255,0.5); } } }

.gameArea {
  display: flex;
  justify-content: flex-start;
  height: 70rem;
  border: 5px solid transparent;
  @media (max-width: 1600px) {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    order: -1;
    height: auto; } }

.gameField {
  display: flex;
  width: 120rem;
  background: url('../img/2.jpg');
  position: relative;
  border: 5px solid black;
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 0 0 10px rgba(180, 180, 180, 0.5); }

.scoreField {
  font-family: "TextMeOne", "Helvetica Neue", sans-serif;
  display: flex;
  flex-direction: column;
  width: 28rem;
  background: rgba(0, 0, 0, 0.5);
  color: $blueNeonColor;
  border: 5px solid transparent;
  border-radius: 20px;
  padding: 0 1rem;
  margin: 0 1rem;
  box-shadow:  0 0 5px rgba(255,255,255, 0.3);

  .grannies-counter {
    font-size: 3.5rem;

    &--danger {
      color: #ff7069; } }

  .unit {
    font-size: 2rem;
    color: #4ba7fc; } }

.gameOver {
  width: 120rem;
  display: flex;
  border-radius: 20px;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  font-family: 'monoton', sans-serif;
  position: relative;
  border: 5px solid black;
  font-size: 7rem;
  background: url('../img/2.jpg');
  color: $blueNeonColor;
  box-shadow: 0 0 10px rgba(180, 180, 180, 0.5); }
