.login-notice {
  font-family: 'Sacramento-Regular', sans-serif;

  height: 20rem;
  padding: 1rem;
  font-size: 4rem;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center; }

.login-menu-link {
  font-family: "TextMeOne", sans-serif;
  cursor: pointer;
  text-decoration: none;
  font-weight: bold;
  color: #ffd1d1;

  &:hover {
    color: #ffacb3; } }

.login-notice-text {
  color: $blueNeonColor;
  text-shadow: 0 0 1px;
  text-align: center; }
