.scores {
  font-family: "TextMeOne", "Helvetica Neue", sans-serif;
  font-size: 5rem;
  line-height: 10rem;
  padding: 3rem;
  margin: 25rem auto;
  color: rgba(202,228,225,0.92);
  text-align: center;
  background: rgba(0, 0, 0, 0.7);
  box-shadow: 0 0 1rem 1rem rgba(0, 0, 0, 0.7); }


.scoresTable {
  margin: 1rem auto;
  width: 120rem;
  font-family: "TextMeOne", "Helvetica Neue", sans-serif;
  font-size: 3rem;
  color: rgba(202,228,225,0.92);
  text-align: center;
  vertical-align: center;
  overflow: hidden;
  background: rgba(0,0,0,0.6);
  border-radius: 10px;

  .unit {
    font-size: 1.5rem;
    color: rgba(75, 167, 252, 0.6); }

  &__link {
    text-decoration: none;
    color: rgba(202,228,225,0.92);

    &:hover {
      color: rgb(81, 184, 228); } }

  &__header {
    height: 5rem;

    &__cell {
      border-radius: 10px;
      box-shadow:  0 0 5px rgba(255,255,255, 0.3); }

    &__no-scores {} }

  &__row {

    &__cell {} } }


.userScorePageTitle {
  margin: 0 auto;
  font-family: "TextMeOne", sans-serif;
  font-size: 5rem;
  color: rgba(202,228,225,0.92); }

.userScorePage {
  width: 120rem;
  color: rgba(202,228,225,0.92);
  display: flex;
  align-items: flex-start;
  justify-content: center;
  margin: 5rem auto;
  font-size: 3rem;
  font-family: "TextMeOne", "Helvetica Neue", sans-serif;

  &__img {
    width: 30rem;
    height: 35rem;
    margin: 0 1rem;
    box-shadow: 0 0 5px rgba(255,255,255, 1), 0 0 15px rgba(255,255,255, 1);
    border-radius: 10px;
    background: url("../img/ava2.jpg") center center no-repeat;
    background-size: cover; }


  &__infoBlock {
    &__info {} } }
